import "./App.css";
import xlsxParser from "xls-parser";
import { useEffect, useRef, useState } from "react";
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';

function App() {
  const [state, setState] = useState({
    checkedUsers: [],
    file: {},
    users: []
  });

  const fileRef = useRef(null);

  useEffect(() => {
    fileRef.current.onchange = () => {
      const selectedFile = fileRef.current.files[0];

      xlsxParser.onFileSelection(selectedFile, { showNullProperties: true, hideEmptyRows: false }).then((data) => {
        const users = new Set()

        data['']
          .filter(el => el[''])
          .forEach(el => users.add(el['']))

        setState({
          file: data,
          users: [...users],
          checkedUsers: []
        });
      });
    };
  }, []);

  const summTime = (t1, t2) => {
    return ((+t1 * 100 + t2 * 100) / 100).toFixed(2);
  }

  const addToTable = (user, event) => {
    if (event.currentTarget.className) {
      event.currentTarget.className = ""

      setState({ ...state, checkedUsers: [ ...state.checkedUsers.filter(data => data.userName !== user) ]})
    } else {
      event.currentTarget.className = "checked"
    
      const currentProjects = state.file[''].reduce((acc, row) => {
        if (row[''] === user) {
          acc.report[row['']] = {
            user: 0,
            other: 0
          }
        }
  
        return acc
      }, { userName: user, report: {}})
  
      state.file[''].forEach(row => {
        if (Object.keys(currentProjects.report).includes(row[''])) {
          if (row[''] === currentProjects.userName) {
            currentProjects.report[row['']].user = summTime(currentProjects.report[row['']].user, +row[' '])
          } else {
            currentProjects.report[row['']].other = summTime(currentProjects.report[row['']].other, +row[' '])
          }
        }
      })

      const { allUserTime, allOtherTime } = Object.values(currentProjects.report).reduce((acc, { user, other }) => {
        acc.allUserTime = summTime(acc.allUserTime, user);
        acc.allOtherTime = summTime(acc.allOtherTime, other);
        return acc
      }, { allUserTime: 0, allOtherTime: 0 })
  
      currentProjects.allUserTime = allUserTime;
      currentProjects.allOtherTime = allOtherTime;
      currentProjects.allTime = summTime(allUserTime, allOtherTime);
  
      
      console.log(currentProjects)
      setState({ ...state, checkedUsers: [ ...state.checkedUsers, currentProjects ]})
    }
  }

  const addFileWithReport = () => {
    fileRef.current.click();
  }

  return (
    <div className="App">
      <Button variant="primary" style={{margin: 10}} onClick={() => addFileWithReport()}>   </Button>{' '}

      <input ref={fileRef} type="file" hidden/>

      <div style={{ display: 'flex', maxHeight: '95vh' }}>
        <Table striped bordered hover style={{maxWidth: 400}}>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {state && state.users.map(user => <tr key={user}><td onClick={(e) => addToTable(user, e)}>{user}</td></tr>)}
          </tbody>
        </Table>

        <Table striped bordered style={{  maxHeight: '100vh'}}>
          <thead>
            <tr>
              <td></td>
              <td></td>
            </tr>
          </thead>

          <tbody>
              {state.checkedUsers.map(checkedUser => {
                return <tr>
                  <td>{checkedUser.userName}</td>

                  <td>
                    <Table>
                      <thead>
                        <tr>
                          <th> </th>
                          <th> </th>
                          <th>  </th>
                          <th> </th>
                        </tr>
                      </thead>

                      <tbody>
                        {Object.entries(checkedUser.report)
                          .map(([key, value]) => <tr>
                            <td>{key}</td>
                            <td>{value.user}</td>
                            <td>{value.other}</td>
                            <td>{summTime(value.user, value.other)}</td>
                          </tr>)}

                        <tr style={{color: 'white', backgroundColor: '#0d6efd7d'}}>
                          <td>  :</td>
                          <td>{checkedUser.allUserTime}</td>
                        </tr>
                        <tr style={{color: 'white', backgroundColor: '#0d6efd7d'}}>
                          <td>   :</td>
                          <td>{checkedUser.allOtherTime}</td>
                        </tr>
                        <tr style={{color: 'white', backgroundColor: '#0d6efd7d'}}>
                          <td> :</td>
                          <td>{checkedUser.allTime}</td>
                        </tr>
                      </tbody>
                    </Table>
                  </td>
                </tr>
              })}
          </tbody>
        </Table>
      </div>
    </div>
  );
}

export default App;
